.example-list {
    max-width: 100%;
    min-height: 50px;
    display: block;
    overflow: hidden;
}

.example-box {
    word-wrap: break-word; /* 단어 단위로 줄바꿈 허용 */
    overflow-wrap: break-word; /* word-wrap과 동일한 기능, IE 호환성 */
    white-space: normal; /* 기본 줄바꿈 동작 */
    min-height: 50px;
    padding: 10px;
    border-bottom: none; /* 기존 border-bottom 제거 */
    color: rgba(0, 0, 0, 0.87);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    cursor: move;
    border: 1px solid rgba(255, 255, 255, 0.2); /* 밝은 테두리 */
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
    font-size: 14px;
  }

/* 유저 메시지 스타일 */
.user-message {
    /* 연한 파란색 배경 */
    /* 둥근 모서리 */
    width: 80%;
    text-align: right;
    margin-left: 20%;
}

/* 봇 메시지 스타일 */
.bot-message {
    /* 흰색 배경 */
    /* 둥근 모서리 */
    width: 80%;
}

.cdk-drag-preview {
    box-sizing: border-box;
    width: 100px;
    height: 100px;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
        0 8px 10px 1px rgba(0, 0, 0, 0.14),
        0 3px 14px 2px rgba(0, 0, 0, 0.12);
    
}

.cdk-drag-animating {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.example-box:last-child {
    border: none;
}

.example-list.cdk-drop-list-dragging .example-box:not(.cdk-drag-placeholder) {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.example-custom-placeholder {
    background: rgba(204, 204, 204, 0.5); /* 반투명 배경, 기존 #ccc 색상 유지 */
    backdrop-filter: blur(10px); /* 흐림 효과 */
    border: 1px solid rgba(255, 255, 255, 0.2); /* 밝은 테두리, 점선 제거 */
    min-height: 50px;
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1); /* 기존 transition 유지 */
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
  }